export default {
	data() {
		return {
			movieList: [],
			movie_title: '',
			page: 1,
			listRows: 20,
			totalPage: 1,
			picUrl: this.$pictureUrl
		}
	},
	onLoad() {
		this.searchData(this.movie_title, this.page, this.listRows);
		uni.setStorageSync('search_flag', false);
		uni.setStorageSync('search_keyword', '');
		this.listenKeyword();// 关键字监听
	},
	onPullDownRefresh(){
		console.log('下拉刷新')
		this.searchData(this.movie_title, this.page, this.listRows);
		this.listenKeyword();// 关键字监听
	},
	onReachBottom() { // 页面触底事件
		console.log('触底了');
		console.log('当前page：'+this.page);
		console.log('总页数：'+this.totalPage);
		this.page = this.page + 1;
		if (this.page > this.totalPage) {
			return ;
		}
		this.searchData(this.movie_title, this.page, this.listRows);
	},
	methods: {
		toSearchDo() { // 打开搜索页
			uni.navigateTo({
				url: 'search_do',
				animationType: 'slide-in-bottom',
				animationDuration: 200
			});
		},
		toDetails(movie_id,score) { // 跳转详情
			uni.navigateTo({
				url: '../details/details?movie_id='+movie_id+'&score='+score
			});
		},
		searchData(movie_title, page, listRows) { // 搜索数据
			uni.request({
				url: this.$serverUrl + '/api/search/selMovieDetails?movie_title=' + movie_title + '&page=' + page + '&listRows=' +
					listRows,
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('请求失败', ret);
						return;
					}
					if (ret.data.code == 0) {
						// 取数据并赋值
						this.movieList = this.movieList.concat(ret.data.data.list);
						this.totalPage = ret.data.data.totalPage;
					} else {
						console.log('未取得数据');
					}
					uni.stopPullDownRefresh();
				}
			});
		},
		listenKeyword() { // 关键字监听
			let me = this;
			let search_keyword = '';
			let k = setInterval(() => {
				search_keyword = uni.getStorageSync('search_keyword');
				this.movie_title = search_keyword;
				if (uni.getStorageSync('search_flag')) {
					uni.setStorageSync('search_flag', false);
					this.movieList = [];
					this.page = 1;
					this.searchData(this.movie_title,this.page,this.listRows);
				}
			
			}, 500);

		}
	}
}
